.containerFAQ {
  display: flex;
  justify-content: center;
  padding-top: 30px;
  margin-bottom: 20px;
}

.QA {
  font-family: "Asap Symbol";
  max-width: 700px;
  display: block;
  justify-content: center;
}

li {
  list-style-type: square;
  font-size: 23px;
}

.questions {
  margin-top: 30px;
}

.faqTitle {
  font-family: "Antonio";
  letter-spacing: 0.5px;
}

@media (max-width: 725px) {
  .containerFAQ {
    margin-right: 20px;
  }
}
